body main > .container {
  padding: 16px 15px 20px;
}
body .footer {
  background-color: #f5f5f5;
  font-size: 0.9em;
  height: 60px;
}
body .footer > .container {
  padding-right: 15px;
  padding-left: 15px;
}

body.dark {
  background-color: #212529 !important;
  color: #fff !important;
}
body.dark input {
  background-color: #212529 !important;
  color: #fff !important;
}
body.dark textarea {
  background-color: #212529 !important;
  color: #fff !important;
}
body.dark div.card {
  background-color: #212529 !important;
  color: #fff !important;
  border: 1px solid #fff !important;
}
body.dark footer {
  background-color: #212529 !important;
  color: #fff !important;
  border-top: 1px solid #6c757d;
}